                    <div v-if="!live_sign" class="col-8" style="margin-top:2em;">
                        <div class="row form-group">
                        <div class="col col-md-12">
                            <div style="display:flex;flex-direction:row">
                            <div v-if="signed==false">
                                <div style="display:flex;flex-direction:row">
                                    <label class="form-control-label" style="margin-right:1em;padding-top:0.4em">    
                                        Signature Security Code : 
                                    </label>
                                        <input type="password" v-model='pwd' class="form-control" style="width:10em;margin-right:1em"/>                   
                                        <button type="button" class="form-control" ref="submitcode" @click="post_ssc" style="width:10em">Sign now !</button>         
                                </div>   
                            </div>
                            <div v-if="signed" style="display:flex;flex-direction:row;">
                                <label class="form-control-label" style="margin-right:1em;padding-top:0.4em;margin-top:2.5em">    
                                    Signature 
                                </label>
                                    <img :src="sign_data[0].signature" style="width:auto;height:5em;border-bottom:1px solid black;margin-left:0.5em"/>        
                            </div>        
                            </div>
                        </div>
                    </div>
                    <div v-html="message">{{message}}</div>
                    </div>    
                </div>            
            </div>
        <!--Input Score--> 
    </div>

        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm" v-if="form">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="button" class="btn btn-danger btn-sm" @click="previous">
              <i class="fa fa-undo"></i> Return
            </button>
        </div>
    </form>
    <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
        <v-data-table
            :headers="headers"
            :items="user_data"
            :search="search"
            class="elevation-1"
            :rows-per-page-items="[10]"
            v-if="hv_user"
            >
        <template v-slot:items="props">
            <tr v-on:click="select_user(props.item)" style='cursor:pointer'>
                <td><a href="" :id= props.item.username v-on:click.prevent="select_user(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            </tr>
        <template v-slot:pageText="props">
            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
        </template>
        
        </template>
        </v-data-table>  
    </div>
</div>

</template>


<script>
function getURLParameter(name) {
  return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.search) || [null, ''])[1].replace(/\+/g, '%20')) || null;
}
import axios from 'axios';
export default {
    data(){          
        return{
            DQ_counter:0,
            company_stat:[],
            user_type:['teacher','teacher'],
            interval: null,
            scored_counter:0,
            total_counter:0,
            signed_combined:false,
            sub_header:'',
            signed_declar:false,
            iframe:'',
            sub_view:'regular',
            user_rank:'',
            all_scored:false,
            phase:'',
            params: window.location.href.substr(window.location.href.indexOf('?')),
            ret_url_sign:'',
            sign_data:[],
            signed:false,
            pwd:'',
            message:'',
            scored:false,
            whoami:'',
            live_sign:false,
            isuser_list:[],
            inlist:false,
            is_sys_user:false,
            sign:'',
            score:[],
            sum:[],
            e3:false,
            score_rank:[],
            comp_list:[],
            company:[],
            counter_company:0,
            principal_p:'',
            principal_k:'',
            fetch_list:[],
            isSET:false,
            counter_cat:1,
            score_cat:[],
            score_percentage:[],
            display_tag:'',
            isTWQA:false,
            tender_list:[],
            isSSB:false,
            ready:false,
            ssb:'',
            teacher:[],
            counter_teacher:1,
            principal:'',
            imc:'',
            imc_ssb:'',
            pta:'',
            sgm:'',
            sgm_list:[],
            form:false,
            header_text:'Tender Meeting',
            proc_id:'',
            entry:0, 
            user_data:[],
            hv_user:false,
            teacher_selected:[],
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'name',sortable: false},
            ], 
            user_index:0,
        }  
    },
async created(){
    this.create_load();
    this.ready = true;       
},
beforeDestroy () {
	clearInterval(this.interval);
},
    methods:{
        DQ(id){
            var i =0;
            var DQ_val = 0;
            for(i=0;i<this.counter_company;i++){
                if(this.company_stat[i]=='DQ'){
                    DQ_val++;
                    var n = 0;
                    for(n=0;n<this.cat_list.length;n++){
                        this.score_rank[n][i] = 0;
                    }
                    this.sum[i] = 0;
                };
            }
            this.DQ_counter = DQ_val;
            this.cal_sum();
            this.$forceUpdate();
        },
        search_user(n){
            this.teacher_selected[n]='';
            var formData = new FormData();
            formData.append('key', this.teacher[n]);
            axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_user=false; 
                    } else {
                        this.user_index = n;
                        this.user_data = response.data;
                        this.hv_user=true;
                    }
                }); 
        },
        select_user(event){
            var i = this.user_index;
            this.teacher[i] = event.name;
            this.teacher_selected[i] = event.username;
            this.hv_user = false;
            this.$forceUpdate();
        },
        check_val(x,y){
            if(this.score_rank[x][y]==''){
                this.score_rank[x][y]=0;
            } else {
                this.score_rank[x][y]='';
            }           
            this.$forceUpdate();
        },
        async reload_data(){
            var formData = new FormData();
            formData.append('proc_id',this.proc_id);
            await axios.post('Procurement/procurement/backend/allscored_check.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='yes'){
                                this.all_scored=true;
                            }
                        }
                    )
            await axios.post('Procurement/procurement/backend/wait_counter.php',formData)
                    .then(
                        (response)=>{
                            this.scored_counter = response.data[0];
                            this.total_counter = response.data[1];
                        }
                    )
            await axios.post('Procurement/procurement/backend/inlist_tender_meeting.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='empty')){
                                this.inlist = true;
                                this.isuser_list = response.data;
                                this.whoami = this.isuser_list[0].name;
                                this.user_rank = this.isuser_list[0].rank;
                                var scored = this.isuser_list[0].scored;
                                if(scored=='true'){
                                    this.scored=true;
                                }
                                var signed = this.isuser_list[0].sign_code;
                                if(!(signed.length==0)){
                                    this.signed_declar = true;
                                }
                                var signed_combine = this.isuser_list[0].combined_sign;
                                if(signed_combine=='true'){
                                    this.signed_combined = true;
                                }
                                var i = 0;
                                for(i=0;i<this.isuser_list.length;i++){
                                    if((this.isuser_list[i].rank=='IMC Manager')||(this.isuser_list[i].rank=='PTA Representative')){
                                        this.live_sign = true;
                                    }
                                }
                            } else if((this.isTWQA==false)&(response.data=='empty')){
                                this.$alert('You are not invited to this tender meeting');
                                this.$router.go(-1);
                            }
                        }
                    )
                    this.update_phase();
        },
        async create_load(){
             this.proc_id = document.getElementById('tmp_id').value;
                let param = new URLSearchParams(this.params);
                if(param.has('proc_id')){
                    this.proc_id = param.get('proc_id');   
                    } 
                var formData = new FormData();
                formData.append('proc_id',this.proc_id);
                await axios.post('Procurement/procurement/backend/attendence_list_fetch.php',formData)
                    .then(
                        (response)=>{
                            var id=0;
                            if(!(response.data=='empty')){
                                this.fetch_list = response.data;
                                this.imc = this.fetch_list[id].name; //0    
                                this.principal = this.fetch_list[id+1].name; //2
                                if(this.fetch_list[id+2].rank=='Principal(P)'){                       
                                    this.principal_p = this.fetch_list[id+2].name;  //2(ssb)
                                    this.principal_k = this.fetch_list[id+3].name;  //3(ssb)
                                    id = id+1;
                                } else {
                                    this.pta = this.fetch_list[id+2].name;  //2(non ssb)
                                }                                       
                                this.sgm = this.fetch_list[id+3].username;  //ssb:4 non-ssb:3
                                var i = id+4;
                                this.counter_teacher = this.counter_teacher-2;
                                var y = 0;
                                for(i=(id+4);i<this.fetch_list.length;i++){
                                    this.user_type[y] = this.fetch_list[i].rank;
                                    this.teacher_selected[y] = this.fetch_list[i].username;
                                    this.teacher[y] = this.fetch_list[i].name;
                                    this.counter_teacher++;
                                    y++;
                                    
                                }
                            }
                        }
                    )
                await axios.post('Procurement/procurement/backend/fetch_score_sheet.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='Empty')){
                                this.cat_list = response.data;
                                var i = 0;
                                this.counter_cat = this.cat_list.length-1;
                                for(i=0;i<this.cat_list.length;i++){
                                    this.score_cat[i] = this.cat_list[i].category;
                                    this.score_percentage[i] = this.cat_list[i].percentage;
                                }
                            }
                        }
                    )    
                await axios.get('Procurement/procurement/backend/isTWQA.php')
                    .then(
                        (response)=>{
                            if(response.data=='exist'){
                                this.entry = 0;
                                this.isTWQA = true;
                            }
                        }
                    )  
                await axios.post('Procurement/procurement/backend/allscored_check.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='yes'){
                                this.all_scored=true;
                            }
                        }
                    )     
                await axios.post('Procurement/procurement/backend/inlist_tender_meeting.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='empty')){
                                this.inlist = true;
                                this.isuser_list = response.data;
                                this.whoami = this.isuser_list[0].name;
                                this.user_rank = this.isuser_list[0].rank;
                                var scored = this.isuser_list[0].scored;
                                if(scored=='true'){
                                    this.scored=true;
                                }
                                var signed = this.isuser_list[0].sign_code;
                                if(!(signed.length==0)){
                                    this.signed_declar = true;
                                }
                                var signed_combine = this.isuser_list[0].combined_sign;
                                if(signed_combine=='true'){
                                    this.signed_combined = true;
                                }
                                var i = 0;
                                for(i=0;i<this.isuser_list.length;i++){
                                    if((this.isuser_list[i].rank=='IMC Manager')||(this.isuser_list[i].rank=='PTA Representative')){
                                        this.live_sign = true;
                                    }
                                }
                            } else if((this.isTWQA==false)&(response.data=='empty')){
                                this.$alert('You are not invited to this tender meeting');
                                this.$router.go(-1);
                            }
                        }
                    ) 
                await axios.post('Procurement/procurement/backend/tender_meeting_isssb.php',formData)
                    .then(
                        (response)=>{
                            this.tender_list = response.data;
                            this.ssb = this.tender_list[0].ssb;
                            this.display_tag = this.tender_list[0].display_tag;
                            this.phase = this.tender_list[0].phase;
                            if(this.ssb=='yes'){
                                this.isSSB = true;
                            } else {
                                this.isSSB = false;
                            }
                        }
                    )
                await axios.get('Procurement/procurement/backend/fetch_smg_list.php')
                    .then(
                        (response)=>{
                            this.sgm_list = response.data;
                        }
                    )
                var formData = new FormData();
                formData.append('proc_id',this.proc_id);
                await axios.post('Procurement/procurement/backend/wait_counter.php',formData)
                    .then(
                        (response)=>{
                            this.scored_counter = response.data[0];
                            this.total_counter = response.data[1];
                        }
                    )
                this.loadData();
        },
        async select_cat(cat){           
            if(cat == 1){
                if(this.isTWQA){
                    this.header_text = 'Tender Declaration Setup';
                    this.form = true;
                    this.entry = cat;
                } else {
                    this.$alert('No Permission');
                }
            } else if(cat == 2){
                if(this.isTWQA){
                    this.header_text = 'Score Sheet Setup';
                    this.form = true;
                    this.entry = cat;
                } else {
                    this.$alert('No Permission');
                }
            } else if(cat==3){
                this.sub_header = ' > Enter Score';
                this.entry = cat;
                //fetch company + category info for score sheet 
                var formData = new FormData();
                formData.append('proc_id',this.proc_id);
                await axios.post('Procurement/procurement/backend/score_sheet_comp.php',formData)
                    .then(
                        (response)=>{                            
                            var i = 0;
                            var y = 0;
                            var offset = '';
                            if(!(response.data=='empty')){
                                this.comp_list = response.data; 
                                var counter = this.comp_list.length;                              
                                for(i=0;i<counter;i++){
                                    var check = this.comp_list[i].company_name;
                                    if(offset.includes(check)==false){
                                        offset = offset + this.comp_list[i].company_name;
                                        this.company[y] = this.comp_list[i].company_name;
                                        this.company_stat[y] = 'active';
                                        y++;
                                    } 
                                }
                            this.counter_company = y;
                            }
                        }
                    )
                    var l = 0;
                    var c = 0;
                    for(c=0;c<this.cat_list.length;c++){
                        this.score_rank[c] = [];
                        this.score[c]=[];
                        for(l=0;l<this.counter_company;l++){
                            this.score_rank[c][l]=0;
                            this.score[c][l]=0;
                        }
                    }
                    this.form = true;
                    this.e3 = true;
                //fetch company + category info for score sheet 
            }
        },
        async previous(){
            if(this.entry>0){
                await this.reload_data();
                this.header_text = 'Tender Meeting';
                this.form = false;
                this.entry=0;
                this.sub_view='regular';
                this.sub_header='';
            } else {
                this.$router.go(-1);
            }

        },
        reload_score_sheet(){
            var formData = new FormData();
            formData.append('proc_id',this.proc_id);
            axios.post('Procurement/procurement/backend/fetch_score_sheet.php',formData)
            .then(
            (response)=>{
                if(!(response.data=='Empty')){
                    this.cat_list = response.data;
                    var i = 0;
                    this.counter_cat = this.cat_list.length-1;
                    for(i=0;i<this.cat_list.length;i++){
                        this.score_cat[i] = this.cat_list[i].category;
                        this.score_percentage[i] = this.cat_list[i].percentage;
                    }
                }
            }
        )
        },
        add(){         
            var i = this.counter_teacher+1;
            if((this.user_type[i]==null)||(this.user_type[i]==undefined)||(this.user_type[i]=='')){
                this.user_type[i] = "teacher";
            }        
            this.counter_teacher++;
        },
        open_full(){
            window.open(this.iframe);
        },
        declar_form(){
            this.entry = 5;
            this.sub_view = 'declar';
            this.sub_header=' > Declaration Form';
            var url = "src/Procurement/procurement/backend/declaration_form.php?proc_id=" + this.proc_id;
            this.iframe = url;
        },
        combined_score_sheet(){
            this.entry = 6;
            this.sub_view = 'combined';
            this.sub_header=' > Combined Score Sheet';
            var url = "src/Procurement/procurement/backend/combined_score_sheet.php?proc_id=" + this.proc_id;
            this.iframe = url;
        },
        remove(){
           this.counter_teacher--; 
        },
        add_cat(){
            if(this.counter_cat<4){
                this.counter_cat++;
            }
        },
        remove_cat(){
            if(this.counter_cat>1){
                this.counter_cat--;
            }
        },
        cal_sum(){
            var i = 0;
            var j = 0;
            var row_sum = [];
            //initialize setup
            for(i=0;i<this.cat_list.length;i++){
                row_sum[i] = 0;
                for(j=0;j<this.counter_company;j++){
                    if(this.score_rank[i][j]>0){
                        row_sum[i] = Number(row_sum[i])+ (this.counter_company-this.DQ_counter)-(Number(this.score_rank[i][j])-1);      
                    }           
                }
            }      
            //initialize setup
            //calculation
            var x = 0;
            var y = 0;
            var sum_tmp = 0;

            for(x=0;x<this.counter_company;x++){
                if(this.company_stat[x]!='DQ'){
                    var sum_tmp = 0;
                    this.sum[x] = 0;
                    for(y=0;y<this.cat_list.length;y++){
                        if(this.score_rank[y][x]>0){
                            sum_tmp = ((this.counter_company-this.DQ_counter)-(this.score_rank[y][x]-1))/row_sum[y]; 
                            sum_tmp = sum_tmp*Number(this.score_percentage[y]);
                        } else {
                            sum_tmp = 0;
                        }                       
                        this.sum[x] = this.sum[x] + sum_tmp;
                        this.sum[x] = Math.round(this.sum[x]*100)/100;
                    }                     
                } else {
                    this.sum[x] = 0;
                }
            }
            this.$forceUpdate();
            //calculation
        },
        undo(){
            if(this.sub_view=='declar'){
                this.$refs.signaturePad_declar.undoSignature();
            } else if(this.sub_view=='combined'){
                this.$refs.signaturePad_combine.undoSignature(); 
            } else {
                this.$refs.signaturePad.undoSignature();  
            }        
        },
        save(){
        const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
        if(isEmpty){
            this.$alert('Signature board cannot be empty!');
        } else {
            this.sign = data;

        }    
        },
        post_ssc(event){
            var formData = new FormData();
            formData.append('pwd',this.pwd);
            axios.post('Procurement/reimbursement/backend/check_sign.php',formData)
                .then(
                    (response)=>{
                        this.sign_data = response.data;
                        if(response.data[0].signature==undefined){
                            this.$alert(response.data).then(
                                ()=>{
                                    this.pwd = '';
                                }
                            )
                        } else {
                            this.signed=true;
                            this.message='';
                            this.pwd='';
                        }
                    }
                )
        },
        sign_declar_pw(){
            var formData = new FormData();
            formData.append('pw',this.pwd);
            formData.append('proc_id',this.proc_id);
            //if(confirm("Confirm signing declaration form?")==true){
            this.$confirm('Confirm signing declaration form?').then(
                    ()=>{
                        axios.post('Procurement/procurement/backend/sign_declar.php',formData)
                        .then(
                            (response)=>{
                                this.sign_data = response.data;
                                if(response.data=="Successfully Signed !"){
                                    this.$alert(response.data);
                                    this.reload_data();
                                    this.message=''; 
                                    this.signed_declar=true;
                                    this.sub_view='regular';
                                    this.entry=0;
                                    this.sub_header='';
                                    this.pwd='';
                                    this.$forceUpdate(); 
                                } else {
                                    this.message = response.data;
                                }
                            }
                        )
                    }
                )
        },
        sign_combine_pw(){
            var formData = new FormData();
            formData.append('name',this.whoami);
            formData.append('pw',this.pwd);
            formData.append('proc_id',this.proc_id);
            //if(confirm("Confirm signing combined score sheet?")==true){
                this.$confirm('Confirm signing combined score sheet?').then(
                    ()=>{
                        axios.post('Procurement/procurement/backend/update_combine_sign_pw.php',formData)
                        .then(
                            (response)=>{
                                this.sign_data = response.data;
                                if(response.data=="Successfully Signed !"){
                                    this.$alert(response.data);
                                    this.reload_data();
                                    this.update_phase();
                                    this.message=''; 
                                    this.signed_combined=true;
                                    /*
                                    this.sub_view='regular';
                                    this.entry=0;
                                    this.sub_header='';
                                    this.pwd='';
                                    */
                                    this.$forceUpdate(); 
                                } else {
                                    this.message = response.data;
                                }
                            }
                        )
                    }
                )
        },
        summary(){
            var url = "src/Procurement/procurement/backend/ret_stat_cat4.php?p_id=" + this.proc_id + "&name=" + this.whoami;
            window.open(url);
        },
        indv_score_sheet(){
            var url = "src/Procurement/procurement/backend/score_sheet_ind.php?proc_id=" + this.proc_id;
            window.open(url);
        },
        update_phase(){
            var formData = new FormData();
            formData.append('proc_id',this.proc_id);
            axios.post('Procurement/procurement/backend/tender_meeting_isssb.php',formData)
                    .then(
                        (response)=>{
                            this.tender_list = response.data;
                            this.phase = this.tender_list[0].phase;
                        }
                    )
        },
        sign_declar(){
            const { isEmpty, data } = this.$refs.signaturePad_declar.saveSignature();
            if(isEmpty){
                this.$alert('Please Sign');
            } else {
                //if(confirm("Confirm signing declaration form?")==true){
                this.$confirm("Confirm signing declaration form?").then(
                    ()=>{
                        this.sign = data;
                        var formData = new FormData();
                        formData.append('sign',this.sign);
                        formData.append('proc_id',this.proc_id);
                        axios.post('Procurement/procurement/backend/live_sign_attend_post.php',formData)
                            .then(
                                (response)=>{
                                    if(response.data=="OK"){
                                        this.update_phase();
                                        this.reload_data();
                                        this.$forceUpdate();
                                        this.sub_view='regular';
                                        this.signed_declar=true;
                                        this.entry=0;
                                        this.sub_header='';                                                                      
                                    } else {
                                        this.$alert(response.data);
                                    }
                                }
                            )
                        }
                )                   
                } 
            //} 
        },
        loadData(){
            if(this.scored_counter==this.total_counter){
                clearInterval(this.interval);
            } else {
            this.interval = setInterval(() => {
            var formData = new FormData();
                formData.append('proc_id',this.proc_id);
                axios.post('Procurement/procurement/backend/wait_counter.php',formData)
                    .then(
                        (response)=>{
                            this.scored_counter = response.data[0];
                            this.total_counter = response.data[1];
                        }
                    )
                }, 10000)
            }
        },
        sign_combine(){
            const { isEmpty, data } = this.$refs.signaturePad_combine.saveSignature();            
            if(isEmpty){
                this.$alert('Please Sign');
            } else {
                this.sign = data;
                var formData = new FormData();
                formData.append('live','live');
                formData.append('access_name',this.whoami);
                formData.append('proc_id',this.proc_id);
                formData.append('sign_code',this.sign);
                axios.post('Procurement/procurement/backend/update_combine_sign_pw.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='Successfully Signed !'){
                                this.$alert(response.data);
                                this.update_phase();
                                this.reload_data();
                                //this.entry=0;
                                this.$forceUpdate();                              
                                //this.entry=0;
                                this.signed_combined = true;
                                //this.sub_header='';                                                              
                            }
                        }
                    )
            }
        },
        handleSubmit(){
            if(this.entry==1){
                //attendence list
                var formData = new FormData();
                formData.append('proc_id',this.proc_id);
                formData.append('manager',this.imc);
                formData.append('ssb',this.isSSB);
                if(this.isSSB == true){
                    formData.append('principal_p',this.principal_p);
                    formData.append('principal_k',this.principal_k);
                } else {
                    formData.append('pta',this.pta);    
                }
                formData.append('principal',this.principal);
                formData.append('sgm',this.sgm);                              
                formData.append('counter',this.counter_teacher);
                var i = 0;
                for(i=0;i<this.counter_teacher+1;i++){
                    formData.append('usertype_'+i,this.user_type[i]);
                    formData.append('teacher_'+i,this.teacher_selected[i]);
                }
                var confirm_msg = "Confirm Submitting Declaration Form Name List?";
                //if(confirm(confirm_msg)){
                this.$confirm(confirm_msg).then(
                    ()=>{
                        axios.post('Procurement/procurement/backend/attendence_setup.php',formData)
                        .then(
                            (response)=>{
                                if(response.data=='OK'){
                                    this.$alert('Successfully Submitted, Return to previous page');
                                    this.$router.go(-1);
                                } else {
                                    this.$alert(response.data);
                                }
                            }
                        )
                    }
                )               
                //}
                //attendence list
            } else if(this.entry==2){
                var formData = new FormData();
                var i = 0 ;
                var check_p = 0;
                formData.append('proc_id',this.proc_id);
                formData.append('counter',this.counter_cat);
                for(i=0;i<this.counter_cat+1;i++){
                    formData.append('cat_'+i,this.score_cat[i]);
                    formData.append('percent_'+i,this.score_percentage[i]);
                    check_p = check_p + Number(this.score_percentage[i]);
                }
                if(check_p==100){
                    axios.post('Procurement/procurement/backend/insert_score_sheet.php',formData)
                        .then(
                            (response)=>{
                                if(response.data=='OK'){
                                    this.reload_score_sheet();
                                    this.reload_data();
                                    this.sub_view='regular';
                                    this.entry=0;
                                    this.sub_header='';
                                    this.scored=true;
                                } else {
                                    this.$alert(response.data);
                                }
                            }
                        )  
                } else {
                    this.$alert('Please Ensure Total Percentage Equals to 100');
                }
            } else if((this.entry==3)&&(this.live_sign)){
                const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
                if(isEmpty){
                    this.$alert('Signature board cannot be empty!');
                } else {                   
                    var checksum = 0;
                    this.sign = data;                       
                    var formData = new FormData();
                    var i = 0;
                    var j = 0;
                    formData.append('sign_code',this.sign);
                    formData.append('user_rank',this.user_rank);
                    formData.append('proc_id',this.proc_id);
                    formData.append('name',this.whoami);
                    formData.append('counter_cat',this.cat_list.length);
                    formData.append('counter_comp',this.counter_company);
                    for(i=0;i<this.counter_company;i++){
                        formData.append('company_'+i,this.company[i]);
                        for(j=0;j<this.cat_list.length;j++){
                            formData.append('com_'+i+'_cat_'+j,this.score_rank[j][i]);
                        }
                        checksum = checksum + this.sum[i];
                        formData.append('sum_'+i,this.sum[i]);
                    }
                    if(checksum>99){
                        this.$confirm('Confirm submitting individual score sheet?').then(
                            ()=>{
                                axios.post('Procurement/procurement/backend/insert_personal_score.php',formData)
                                .then(
                                    (response)=>{
                                        if(response.data=='OK'){
                                            this.$alert('Successfully Submitted Score Sheet');
                                            var url = "src/Procurement/procurement/backend/combined_score_sheet.php?proc_id=" + this.proc_id;
                                            this.iframe = url;
                                            this.reload_data();
                                            this.sub_view='combined';
                                        } else {
                                            this.$alert(response.data);
                                        }
                                    }
                                )
                            }
                        )                                        
                    } else {
                        this.$alert("Please Input all ranks");
                    }
                } 
            } else if((this.entry==3)&&(this.live_sign==false)){
                var checksum = 0;
                if(this.signed){
                    var formData = new FormData();
                    var i = 0;
                    var j = 0;
                    formData.append('sign_code',this.sign_data[0].signature);               
                    formData.append('proc_id',this.proc_id);
                    formData.append('name',this.whoami);
                    formData.append('user_rank',this.user_rank);
                    formData.append('counter_cat',this.cat_list.length);
                    formData.append('counter_comp',this.counter_company);
                    for(i=0;i<this.counter_company;i++){
                        formData.append('company_'+i,this.company[i]);
                        for(j=0;j<this.cat_list.length;j++){
                            formData.append('com_'+i+'_cat_'+j,this.score_rank[j][i]);
                        }
                        checksum = checksum + this.sum[i];
                        formData.append('sum_'+i,this.sum[i]);
                    }
                    if(checksum>99){
                            axios.post('Procurement/procurement/backend/insert_personal_score.php',formData)
                                .then(
                                    (response)=>{
                                        if(response.data=='OK'){
                                            this.$alert('Successfully Submitted Score Sheet');
                                            var url = "src/Procurement/procurement/backend/combined_score_sheet.php?proc_id=" + this.proc_id;
                                            this.iframe = url;
                                            this.reload_data();
                                            this.sub_view='combined';
                                        } else {
                                            this.$alert(response.data);
                                        }
                                    }
                                )
                        } else {
                            this.$alert("Please Input all ranks");
                        }
                    this.signed = false;
                    this.$forceUpdate();
                } else {
                    this.$alert('Please Sign');
                }
            }
        }
    },

}
</script>


<style scoped>
    .unassign{
        background-color:yellow;
    }
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>
<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
.company_name {
    text-align:center;
    margin-left:2.5%;
    margin-right:2.5%
}
</style>



